import { createContext, Dispatch, SetStateAction, useCallback, useContext, useEffect, useState } from 'react';

import { OpenFin } from '@openfin/core';

export enum SidePanelComponent {
    Bookmarks = 'bookmarks',
    AICompanion = 'ai-companion'
}

const SidePanelContext = createContext<{
    isSidePanelOpen: boolean;
    isAICompanionEnabled: boolean;
    activeComponent: SidePanelComponent;
    toggleSidePanelComponent: (component: SidePanelComponent) => void;
    setActiveComponent: Dispatch<SetStateAction<SidePanelComponent>>;
}>({
    isSidePanelOpen: false,
    isAICompanionEnabled: false,
    activeComponent: SidePanelComponent.Bookmarks,
    toggleSidePanelComponent: () => {
        return;
    },
    setActiveComponent: () => {
        return;
    }
});

export const SidePanelContextProvider = ({ children }: { children?: React.ReactNode }) => {
    const [isAICompanionEnabled, setIsAICompanionEnabled] = useState<boolean>(false);
    const [isSidePanelOpen, setIsSidePanelOpen] = useState<boolean>(null);
    const [activeComponent, setActiveComponent] = useState<SidePanelComponent>(null);

    useEffect(() => {
        (async () => {
            const browserWindowOptions = await (fin.me as OpenFin.Window).getOptions();
            setIsAICompanionEnabled(!!browserWindowOptions.workspacePlatform.isAiEnabled);
        })();
    }, []);

    const toggleSidePanelComponent = useCallback((component: SidePanelComponent) => {
        setActiveComponent((prevComponent) => {
            if (prevComponent !== component) {
                setIsSidePanelOpen(true);
                return component;
            } else {
                setIsSidePanelOpen((prev) => !prev);
            }
        });
    }, []);

    return (
        <SidePanelContext.Provider
            value={{
                isSidePanelOpen,
                isAICompanionEnabled,
                activeComponent,
                toggleSidePanelComponent,
                setActiveComponent
            }}
        >
            {children}
        </SidePanelContext.Provider>
    );
};

export const useSidePanelContext = () => useContext(SidePanelContext);

/**
 * Custom hook to determine whether a specific side panel component should apply 'display: none' CSS property.
 * This usage allows side panel components to hide while not losing its state while a user is switching between panel components.
 *
 * @param {SidePanelComponent} component - The side panel component to check.
 * @returns {boolean} `true` if the side panel is open and the active component is not the specified component, otherwise `false`.
 */
export const useShouldHidePanelComponent = (component: SidePanelComponent) => {
    const { isSidePanelOpen, activeComponent } = useSidePanelContext();
    return isSidePanelOpen && activeComponent !== component;
};
