import { createContext, useCallback, useContext, useEffect, useMemo, useState } from 'react';

import type OpenFin from '@openfin/core';

type WindowState = 'focused' | 'view-focused' | 'blurred';

type WindowStyle = 'focused' | 'blurred';

type WindowStateContext = {
    // The OS state of the window
    state: WindowState;
    // Which style mode to use - can be overriden temporarily.
    style: WindowStyle;
    // Allows a temporary override of window style
    withWindowStyleOverride: (style: WindowStyle, producer: () => Promise<void> | void) => Promise<void>;
};

const WindowStateContext = createContext<WindowStateContext>({
    state: 'focused',
    style: 'focused',
    withWindowStyleOverride: () => void 0
});

const wait = (time: number): Promise<void> => new Promise((resolve) => setTimeout(resolve, time));

/**
 * Observes the window state and patches the issue (WAAS-717) that causes windows to briefly appear blurred when
 * a view is focused within it.
 *
 * @param onStateChanged Emits the current state of the window.
 * @param browserWindow The window to observe.
 * @param gracePeriod The period to wait (ms) for a 'view-focused' event after we recieve a 'window-blurred' event before deciding the window really did blur.
 * @returns Disposer function, which tears down event listeners.
 */
const observeWindowState = (
    onStateChanged: (state: WindowState) => void,
    browserWindow: OpenFin.Window,
    gracePeriod = 50
): { dispose: () => Promise<void> } => {
    let state: 'focused' | 'view-focused' | 'blurred' | 'pending-blur';
    const onViewFocus = () => {
        state = 'view-focused';
        onStateChanged('view-focused');
    };
    const onWindowFocused = () => {
        state = 'focused';
        onStateChanged('focused');
    };
    const onWindowBlurred = async () => {
        state = 'pending-blur';
        await wait(gracePeriod);
        if (state === 'pending-blur') {
            state = 'blurred';
            onStateChanged('blurred');
        }
    };

    browserWindow.addListener('view-focused', onViewFocus);
    browserWindow.addListener('focused', onWindowFocused);
    browserWindow.addListener('blurred', onWindowBlurred);

    return {
        dispose: async () => {
            await Promise.all([
                browserWindow.removeListener('view-focused', onViewFocus),
                browserWindow.removeListener('focused', onWindowFocused),
                browserWindow.removeListener('blurred', onWindowBlurred)
            ]);
        }
    };
};

/**
 * Creates a context which exposes information about the specified window's state
 */
export const WindowStateProvider = (props: { children: React.ReactNode }) => {
    const [windowState, setWindowState] = useState<WindowState>('focused');
    // Forces the window style to a certain state (e.g. focussed)
    const [windowStyleOverride, setWindowStyleOverride] = useState<WindowStyle>();

    const onStateChanged = useCallback((state: WindowState) => {
        setWindowState(state);
    }, []);

    const windowStyle = useMemo(
        () => windowStyleOverride || (windowState === 'blurred' ? 'blurred' : 'focused'),
        [windowState, windowStyleOverride]
    );

    useEffect(() => {
        if (!fin) return;
        const { dispose } = observeWindowState(onStateChanged, fin.Window.getCurrentSync());
        return () => {
            dispose();
        };
    }, [onStateChanged]);

    /**
     * Override hook allowing the window style to be temporarily overriden during transactions such
     * as showing context menus.
     */
    const withWindowStyleOverride = useCallback(
        async (style: WindowStyle, producer: () => Promise<void> | void): Promise<void> => {
            setWindowStyleOverride(style);
            try {
                await producer();
            } finally {
                setWindowStyleOverride(undefined);
            }
        },
        []
    );

    return (
        <WindowStateContext.Provider
            value={{
                state: windowState,
                withWindowStyleOverride,
                style: windowStyle
            }}
        >
            {props.children}
        </WindowStateContext.Provider>
    );
};

/**
 * Tracks the window's current state.
 */
export const useWindowState = () => useContext(WindowStateContext).state;

export const useWithWindowStyleOverride = () => {
    const { withWindowStyleOverride } = useContext(WindowStateContext);
    return withWindowStyleOverride;
};

/**
 * Tracks whether the window or any view within it is focused.
 */
const useWindowFocusState = () => {
    const { style } = useContext(WindowStateContext);
    return style !== 'blurred';
};

export default useWindowFocusState;
