import { createContext, useCallback, useEffect, useState } from 'react';

import { OpenFin } from '@openfin/core';

import { BrowserChannelAction, getBrowserChannelClient } from '@common/api/protocol/browser';

export const BookmarksContext = createContext<{
    mostRecentlyUpdated: number;
    isBookmarksPanelOpen: boolean;
    toggleBookmarks: () => void;
}>({
    mostRecentlyUpdated: undefined,
    isBookmarksPanelOpen: undefined,
    toggleBookmarks: () => {
        return;
    }
});

/**
 * Provides the Bookmarks context to its children.
 *
 * @param {Object} props - The props for the BookmarksContextProvider.
 * @param {React.ReactNode} [props.children] - The child components to be wrapped by the provider.
 * @param {OpenFin.ChannelProvider} [props.channel] - Optional channel override to listen for the RefreshBookmarksInternal event. Defaults to existing browser window channel.
 * @param {() => void} [props.toggleBookmarksOverride] - Optional additional function to call when toggling the bookmarks panel (e.g. to close bookmarks from the X button in Dock).
 * @returns {JSX.Element} The BookmarksContext provider component.
 */
export const BookmarksContextProvider = ({
    children,
    channel: dockChannel,
    toggleBookmarksOverride
}: {
    children?: React.ReactNode;
    channel?: OpenFin.ChannelProvider;
    toggleBookmarksOverride?: () => void;
}) => {
    const [isBookmarksPanelOpen, setIsBookmarksPanelOpen] = useState<boolean>(null);
    const [mostRecentlyUpdated, setMostRecentlyUpdated] = useState<number>(null);

    useEffect(() => {
        let channel: OpenFin.ChannelProvider;

        const initializeChannel = async () => {
            // If creating context from Dock window, use existing Dock channel from context
            channel = dockChannel ?? (await getBrowserChannelClient());
            channel.register(BrowserChannelAction.RefreshBookmarksInternal, () => setMostRecentlyUpdated(Date.now()));
        };

        initializeChannel();

        return () => {
            // If we're using existing Dock channel, it will get cleaned up in the Dock context
            if (channel && !dockChannel) channel.destroy();
        };
    }, [dockChannel]);

    const toggleBookmarks = useCallback(() => {
        if (toggleBookmarksOverride) {
            toggleBookmarksOverride();
        }
        setIsBookmarksPanelOpen((prev) => !prev);
    }, [toggleBookmarksOverride]);

    return (
        <BookmarksContext.Provider value={{ mostRecentlyUpdated, isBookmarksPanelOpen, toggleBookmarks }}>
            {children}
        </BookmarksContext.Provider>
    );
};
