import OpenFin from '@openfin/core';

import { t } from '@common/api/i18next';
import { BookmarkNode } from '@common/api/pages/shapes';

import { getCurrentSync } from '@client-platform/api';

export const ALL_BOOKMARKS_FOLDER_TITLE = '_ALL_BOOKMARKS_';

export const FAVORITES_FOLDER_TITLE = '_FAVORITES_';

export const DOCK_FAVORITE_BOOKMARK_PREFIX = 'dock-favorite-bookmark:';

export type BookmarkFolder = { id: string; title: string };

export type CategorizedBookmarkSearchResults = {
    bookmarksInFolder: BookmarkNode[];
    bookmarksOutsideFolder: BookmarkNode[];
};

export const getBookmarkNodeTitle = (title: string) => {
    switch (title) {
        case ALL_BOOKMARKS_FOLDER_TITLE:
            return t('bookmarks.allBookmarks');
        case FAVORITES_FOLDER_TITLE:
            return t('bookmarks.favorites');
        default:
            return title;
    }
};

export const categorizeSearchResults = (
    bookmarks: BookmarkNode[],
    folderId: string
): CategorizedBookmarkSearchResults => {
    return bookmarks.reduce(
        (accum, bookmark) => {
            if (bookmark.parentId === folderId) {
                accum.bookmarksInFolder.push(bookmark);
            } else {
                accum.bookmarksOutsideFolder.push(bookmark);
            }
            return accum;
        },
        {
            bookmarksInFolder: [],
            bookmarksOutsideFolder: []
        } as CategorizedBookmarkSearchResults
    );
};

export const sortBookmarks = (a: BookmarkNode, b: BookmarkNode) => {
    // Prioritize "All Bookmarks" folder first
    if (!a.url && a.title === ALL_BOOKMARKS_FOLDER_TITLE) {
        return -1;
    } else if (!b.url && b.title === ALL_BOOKMARKS_FOLDER_TITLE) {
        return 1;
    }

    // Fallback to prioritizing "Favorites" folder if "All Bookmarks" isn't prioritized
    if (!a.url && a.title === FAVORITES_FOLDER_TITLE) {
        return -1;
    } else if (!b.url && b.title === FAVORITES_FOLDER_TITLE) {
        return 1;
    }

    // Handle sorting folders over bookmarks
    if (!a.url && b.url) {
        return -1;
    } else if (a.url && !b.url) {
        return 1;
    }

    // Sort by creation date for all remaining items
    return b.dateCreated - a.dateCreated;
};

export const collectFolders = (nodes: BookmarkNode[]): BookmarkFolder[] =>
    nodes
        .reduce((acc: BookmarkFolder[], node) => {
            if (!node.url) {
                acc.push({ id: node.id, title: node.title });
            }
            if (node.children) {
                acc.push(...collectFolders(node.children));
            }
            return acc;
        }, [])
        .sort(sortBookmarks);

export const getBookmarkFoldersList = (rootNode: BookmarkNode) => collectFolders([rootNode]);

export const updateBookmarkFavIcon = async (url: string, selectedView: OpenFin.View | null) => {
    const viewInfo = await selectedView.getInfo();
    const favIcon = viewInfo.favicons.length ? viewInfo.favicons[0] : '';
    const browserApi = getCurrentSync().Browser.wrapSync(fin.Window.getCurrentSync().identity);
    // eslint-disable-next-line no-underscore-dangle
    const result = await browserApi._bookmarks._searchBookmarkNodes(url);
    if (!result.length) {
        console.debug('consolidate icon: bookmark not found');
        return;
    }
    // eslint-disable-next-line no-underscore-dangle
    await browserApi._bookmarks._updateBookmarkNode({ id: result[0].id, changes: { favIcon } });
    console.debug(`consolidate icon update: bookmark ${url} updated with icon`, favIcon);
    // eslint-disable-next-line no-underscore-dangle
    await getCurrentSync()._refreshBookmarksInternal();
};
