import { ChangeEvent, useRef } from 'react';
import { useTranslation } from 'react-i18next';

import styled from 'styled-components';

import { Icon } from '@openfin/ui-library';

import BaseButton from '@common/components/BaseButton/BaseButton';

interface BookmarksSearchInputProps {
    handleSearchInputChange: (event: ChangeEvent<HTMLInputElement>) => void;
    clearSearchInput: () => void;
}

const BookmarksSearchInput: React.FC<BookmarksSearchInputProps> = ({ handleSearchInputChange, clearSearchInput }) => {
    const { t } = useTranslation();
    const inputRef = useRef<HTMLInputElement>();

    const handleClearSearchInput = () => {
        inputRef.current.value = '';
        inputRef.current.focus();
        clearSearchInput();
    };

    return (
        <Container>
            <SearchContainer>
                <SearchInput
                    ref={inputRef}
                    placeholder={t('bookmarks.searchBookmarks')}
                    onChange={handleSearchInputChange}
                />
                <SearchIcon icon="MagnifyingGlassIcon" size="small" />
                <ClearSearchButton aria-label={t('bookmarks.clearSearchInput')} onClick={handleClearSearchInput}>
                    <Icon icon="Cross2Icon" size="small" />
                </ClearSearchButton>
            </SearchContainer>
        </Container>
    );
};

export default BookmarksSearchInput;

const Container = styled.div`
    margin-bottom: ${({ theme }) => theme.px.small};
`;

const SearchContainer = styled.div`
    position: relative;
`;

const SearchIcon = styled(Icon)`
    position: absolute;
    top: 3px;
    left: 3px;

    svg {
        fill: ${({ theme }) => theme.palette.inputColor};
        opacity: 0.75;
    }
`;

const ClearSearchButton = styled(BaseButton)`
    height: ${({ theme }) => theme.px.xxlarge};
    width: ${({ theme }) => theme.px.large};

    position: absolute;
    top: 0px;
    right: ${({ theme }) => theme.px.xsmall};

    &:hover {
        background: unset;

        cursor: unset;
    }

    svg {
        fill: ${({ theme }) => theme.palette.inputColor};
        opacity: 0.75;
    }
`;

const SearchInput = styled.input`
    height: 24px;
    width: 100%;
    padding-left: ${({ theme }) => theme.px.xxlarge};
    padding-right: ${({ theme }) => theme.px.xxlarge};

    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;

    font: var(--openfin-ui-globalTypography);
    font-size: ${({ theme }) => theme.fontSize.base};
    color: ${({ theme }) => theme.palette.inputColor};

    background-color: ${({ theme }) => theme.palette.inputBackground};
    border: 1px solid ${({ theme }) => theme.palette.inputBorder};
    border-radius: ${({ theme }) => theme.radius.small};
    outline: none;

    &:focus,
    &:hover {
        border-color: ${({ theme }) => theme.palette.inputFocused};
    }

    &:focus ~ * svg,
    &:hover ~ * svg {
        opacity: 1; // Brighten icons when search hovered/focused
    }

    &:placeholder-shown ~ ${ClearSearchButton} {
        display: none; // Don't show clear button when search input empty
    }
`;
