import { useTranslation } from 'react-i18next';

import styled from 'styled-components';

import { H2, Icon } from '@openfin/ui-library';

import CloseButtonLarge from '@common/components/CloseButton/CloseButtonLarge';

import { SidePanelComponent, useSidePanelContext } from '@enterprise/context/SidePanelContext';

const BookmarksPanelHeader = ({ onClose }: { onClose?: () => void }) => {
    const { t } = useTranslation();
    const { toggleSidePanelComponent } = useSidePanelContext();

    return (
        <Container>
            <LeftContainer>
                <Icon icon="BookmarkFilled" />
                <Title>{t('bookmarks.bookmarks')}</Title>
            </LeftContainer>
            <CloseButtonLarge
                onClick={() => (onClose ? onClose() : toggleSidePanelComponent(SidePanelComponent.Bookmarks))}
                aria-label={t('bookmarks.closePanel')}
            />
        </Container>
    );
};

export default BookmarksPanelHeader;

const Title = styled(H2)`
    font-size: ${({ theme }) => theme.fontSize.base};
    font-weight: normal;
    color: ${({ theme }) => theme.palette.textDefault};
    line-height: ${({ theme }) => theme.lineHeight.text};
`;

const Container = styled.div`
    height: 48px;
    width: 100%;
    padding: 0 ${({ theme }) => theme.px.base};
    border-bottom: 1px solid ${({ theme }) => theme.palette.background3};

    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-shrink: 0;
`;

const LeftContainer = styled.div`
    display: flex;
    gap: ${({ theme }) => theme.px.xsmall};
    align-items: center;

    svg {
        margin-top: -1px;

        path {
            fill: ${({ theme }) => theme.palette.textDefault};
        }
    }
`;
