import { useTranslation } from 'react-i18next';

import styled from 'styled-components';

import { Icon, Text } from '@openfin/ui-library';

import { ALL_BOOKMARKS_FOLDER_TITLE, getBookmarkNodeTitle } from '@common/utils/bookmarks';

import { BookmarkNode } from '@common/api/pages/shapes';

import BaseButton from '@common/components/BaseButton/BaseButton';

interface BookmarksPanelHeaderProps {
    folder: BookmarkNode;
    handleNavigateToFolder: (folderId: string) => void;
}

const BookmarksFolderHeader: React.FC<BookmarksPanelHeaderProps> = ({ folder, handleNavigateToFolder }) => {
    const { t } = useTranslation();
    const isAllBookmarksFolder = folder?.title === ALL_BOOKMARKS_FOLDER_TITLE;

    return (
        <Container>
            {!isAllBookmarksFolder && (
                <BackButton
                    aria-label={t('bookmarks.exitFolder')}
                    onClick={() => handleNavigateToFolder(folder?.parentId)}
                >
                    <Icon icon="ArrowLeftIcon" />
                </BackButton>
            )}

            <Text>{getBookmarkNodeTitle(folder?.title)}</Text>
        </Container>
    );
};

export default BookmarksFolderHeader;

const Container = styled.div`
    display: flex;
    align-items: center;
    gap: ${({ theme }) => theme.px.small};

    ${Text} {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
`;

const BackButton = styled(BaseButton)`
    width: unset;

    &:hover {
        background: unset;
    }
`;
