import { useCallback, useEffect, useState } from 'react';

import debounce from 'lodash.debounce';

import {
    ALL_BOOKMARKS_FOLDER_TITLE,
    CategorizedBookmarkSearchResults,
    categorizeSearchResults,
    sortBookmarks
} from '@common/utils/bookmarks';
import { currentOFIdentity } from '@common/utils/window';

import { BookmarkNode } from '@common/api/pages/shapes';

import { getCurrentSync } from '@client-platform/api';

/**
 * Custom hook that manages and sorts bookmark search results and returns an object of categorized bookmarks
 * and actions to update the search query.
 *
 * @returns {Object} An object containing the following:
 * - `categorizedSearchResults` : An object containing two arrays, `bookmarksInFolder` and `bookmarksOutsideFolder`.
 * - `handleSearchInputChange` : A function to update the search query phrase to retrieve new search results.
 * - `clearSearchInput` : A function to clear the search query and reset the panel to its previous state.
 */
export const useBookmarksSearch = (
    currentFolder: BookmarkNode,
    setSearchQuery: (query: string) => void,
    updateBookmarksResults: (folderId?: string, navigateToFolder?: boolean) => void
) => {
    const [categorizedSearchResults, setCategorizedSearchResults] = useState<CategorizedBookmarkSearchResults>({
        bookmarksInFolder: [],
        bookmarksOutsideFolder: []
    });

    const updateSearchResults = useCallback(
        async (searchText: string) => {
            let isMounted = true;
            if (searchText.length) {
                const browser = getCurrentSync().Browser.wrapSync(currentOFIdentity);
                // eslint-disable-next-line no-underscore-dangle
                const results = await browser._bookmarks._searchBookmarkNodes(searchText);
                const resultsWithoutFolders = results.filter((node) => node.url).sort(sortBookmarks);

                if (currentFolder.title !== ALL_BOOKMARKS_FOLDER_TITLE) {
                    const categorizedResults = categorizeSearchResults(resultsWithoutFolders, currentFolder.id);
                    setCategorizedSearchResults(categorizedResults);
                } else {
                    setCategorizedSearchResults({
                        bookmarksInFolder: resultsWithoutFolders,
                        bookmarksOutsideFolder: []
                    });
                }
            }
            if (isMounted) {
                setSearchQuery(searchText);
            }
            return () => {
                isMounted = false;
            };
        },
        [currentFolder?.title, currentFolder?.id, setSearchQuery]
    );

    const debouncedUpdateSearchResults = useCallback(debounce(updateSearchResults, 300), [updateSearchResults]);

    useEffect(() => {
        return () => {
            // Cancel any pending debounced calls when the component unmounts
            debouncedUpdateSearchResults.cancel();
        };
    }, [debouncedUpdateSearchResults]);

    const handleSearchInputChange = useCallback(
        (event: React.ChangeEvent<HTMLInputElement>) => {
            debouncedUpdateSearchResults(event.target.value);
        },
        [debouncedUpdateSearchResults]
    );

    const clearSearchInput = useCallback(() => {
        setSearchQuery('');
        updateBookmarksResults(currentFolder.id);
    }, [setSearchQuery, updateBookmarksResults, currentFolder?.id]);

    return { categorizedSearchResults, handleSearchInputChange, clearSearchInput };
};
