import { PropsWithChildren, useMemo } from 'react';

import styled, { css } from 'styled-components';

import { DOCK_FAVORITE_BOOKMARK_PREFIX, FAVORITES_FOLDER_TITLE } from '@common/utils/bookmarks';

import { BookmarkNode } from '@common/api/pages/shapes';

import { CustomIcon } from '@common/components/Icon/CustomIcon';

import BookmarkFavoritesDarkIcon from '@enterprise/assets/icons/bookmark-favorites-dark.svg';
import BookmarkFavoritesLightIcon from '@enterprise/assets/icons/bookmark-favorites-light.svg';
import BookmarkFolderDarkIcon from '@enterprise/assets/icons/bookmark-folder-dark.svg';
import BookmarkFolderLightIcon from '@enterprise/assets/icons/bookmark-folder-light.svg';
import BookmarkFolderOpenDarkIcon from '@enterprise/assets/icons/bookmark-folder-open-dark.svg';
import BookmarkFolderOpenLightIcon from '@enterprise/assets/icons/bookmark-folder-open-light.svg';
import DockFavoritesFolderIcon from '@enterprise/assets/icons/dock-favorites-folder-small.svg';

import { DockEntry } from '@client-platform/index';
import useSystemSchemeCheck from '@common/hooks/useSystemSchemeCheck';
import useWindowFocusState from '@common/hooks/useWindowFocusState';

import { IconWrapper } from './BookmarkItem';

interface BookmarkIconProps {
    bookmark: BookmarkNode;
}

const BookmarkIcon: React.FC<BookmarkIconProps> = ({ bookmark }) => {
    const isBookmarkFolder = !bookmark.url;
    const isFavoritesFolder = isBookmarkFolder && bookmark.title === FAVORITES_FOLDER_TITLE;

    const isDockFolderFavorite = useMemo(() => {
        if (bookmark.url?.startsWith(DOCK_FAVORITE_BOOKMARK_PREFIX)) {
            const jsonString = bookmark.url.substring(DOCK_FAVORITE_BOOKMARK_PREFIX.length);
            const dockEntry = JSON.parse(jsonString) as DockEntry;
            return dockEntry.type === 'folder';
        }
        return false;
    }, [bookmark.url]);

    return (
        <BookmarkIconWrapper>
            {isFavoritesFolder ? (
                <BookmarkFavoritesIcon />
            ) : isBookmarkFolder ? (
                <BookmarkFolderIcon />
            ) : isDockFolderFavorite ? (
                <DockFavoritesFolderIcon />
            ) : (
                <CustomIcon iconURL={bookmark.favIcon} size="base"></CustomIcon>
            )}
        </BookmarkIconWrapper>
    );
};

export default BookmarkIcon;

const FolderIconStyles = css`
    path:nth-of-type(1) {
        fill: ${({ theme }) => theme.palette.background3};
        stroke: ${({ theme }) => theme.palette.textDefault};
    }
    path:nth-of-type(2) {
        fill: ${({ theme }) => theme.palette.brandPrimary};
        stroke: ${({ theme }) => theme.palette.textDefault};
    }
`;

const FavoriteIconStyles = css`
    path {
        fill: ${({ theme }) => theme.palette.brandPrimary};
        stroke: ${({ theme }) => theme.palette.textDefault};
    }
`;

const BookmarkIconWrapper: React.FC<PropsWithChildren> = ({ children }) => {
    const windowFocused = useWindowFocusState();
    return <IconWrapper windowFocused={windowFocused}>{children}</IconWrapper>;
};

const BookmarkFolderIcon: React.FC = () => {
    const { isSchemeDark } = useSystemSchemeCheck();
    return isSchemeDark ? <StyledBookmarkFolderDarkIcon /> : <StyledBookmarkFolderLightIcon />;
};

const BookmarkFavoritesIcon: React.FC = () => {
    const { isSchemeDark } = useSystemSchemeCheck();
    return isSchemeDark ? <StyledBookmarkFavoritesDarkIcon /> : <StyledBookmarkFavoritesLightIcon />;
};

const StyledBookmarkFavoritesDarkIcon = styled(BookmarkFavoritesDarkIcon)`
    ${FavoriteIconStyles}
`;

const StyledBookmarkFavoritesLightIcon = styled(BookmarkFavoritesLightIcon)`
    ${FavoriteIconStyles}
`;

const StyledBookmarkFolderDarkIcon = styled(BookmarkFolderDarkIcon)`
    ${FolderIconStyles}
`;

const StyledBookmarkFolderLightIcon = styled(BookmarkFolderLightIcon)`
    ${FolderIconStyles}
`;

export const BookmarkFolderOpenIcon: React.FC = () => {
    const { isSchemeDark } = useSystemSchemeCheck();
    return isSchemeDark ? <StyledBookmarkFolderOpenDarkIcon /> : <StyledBookmarkFolderOpenLightIcon />;
};

const StyledBookmarkFolderOpenDarkIcon = styled(BookmarkFolderOpenDarkIcon)`
    ${FolderIconStyles}
`;

const StyledBookmarkFolderOpenLightIcon = styled(BookmarkFolderOpenLightIcon)`
    ${FolderIconStyles}
`;
