import { useTranslation } from 'react-i18next';

import styled from 'styled-components';

import { Text } from '@openfin/ui-library';

import {
    ALL_BOOKMARKS_FOLDER_TITLE,
    CategorizedBookmarkSearchResults,
    getBookmarkNodeTitle
} from '@common/utils/bookmarks';

import { BookmarkNode } from '@common/api/pages/shapes';

import BookmarkItem from './BookmarkItem';

interface BookmarksResultsProps {
    currentFolder: BookmarkNode;
    bookmarksList: BookmarkNode[];
    categorizedSearchResults: CategorizedBookmarkSearchResults;
    isSearchQueryResults: boolean;
    renamingNodeId: string | undefined;
    handleNavigateToFolder: (folderId: string) => void;
    setRenamingNode: (id?: string) => void;
}

const BookmarksResults: React.FC<BookmarksResultsProps> = ({
    currentFolder,
    bookmarksList,
    categorizedSearchResults,
    isSearchQueryResults,
    renamingNodeId,
    handleNavigateToFolder,
    setRenamingNode
}) => {
    const { t } = useTranslation();
    const { bookmarksInFolder, bookmarksOutsideFolder } = categorizedSearchResults;
    const inAllBookmarksFolder = currentFolder?.title === ALL_BOOKMARKS_FOLDER_TITLE;

    if (isSearchQueryResults && !inAllBookmarksFolder) {
        const hasResultsInFolder = bookmarksInFolder.length > 0;
        const hasResultsOutsideFolder = bookmarksOutsideFolder.length > 0;

        return (
            <>
                {hasResultsInFolder && (
                    <FolderSearchResultsSection
                        header={t('bookmarks.resultsFoundIn', { name: getBookmarkNodeTitle(currentFolder.title) })}
                        bookmarksList={bookmarksInFolder}
                        renamingNodeId={renamingNodeId}
                        handleNavigateToFolder={handleNavigateToFolder}
                        setRenamingNode={setRenamingNode}
                    />
                )}
                {hasResultsOutsideFolder && (
                    <FolderSearchResultsSection
                        header={t('bookmarks.resultsInOtherFolders')}
                        bookmarksList={bookmarksOutsideFolder}
                        renamingNodeId={renamingNodeId}
                        handleNavigateToFolder={handleNavigateToFolder}
                        setRenamingNode={setRenamingNode}
                    />
                )}
            </>
        );
    }

    const bookmarksToDisplay = isSearchQueryResults ? bookmarksInFolder : bookmarksList;
    return (
        <>
            {bookmarksToDisplay?.map((bookmark) => (
                <BookmarkItem
                    key={bookmark.id}
                    bookmark={bookmark}
                    isRenaming={renamingNodeId === bookmark.id}
                    handleNavigateToFolder={handleNavigateToFolder}
                    setRenamingNode={setRenamingNode}
                />
            ))}
        </>
    );
};

export default BookmarksResults;

interface FolderSearchResultsSectionProps {
    header: string;
    bookmarksList: BookmarkNode[];
    renamingNodeId: string | undefined;
    handleNavigateToFolder: (folderId: string) => void;
    setRenamingNode: (id?: string) => void;
}

const FolderSearchResultsSection: React.FC<FolderSearchResultsSectionProps> = ({
    header,
    bookmarksList,
    renamingNodeId,
    handleNavigateToFolder,
    setRenamingNode
}) => {
    return (
        <>
            <TextHeader>
                <Text>{header}</Text>
            </TextHeader>
            {bookmarksList.map((bookmark) => (
                <BookmarkItem
                    key={bookmark.id}
                    bookmark={bookmark}
                    isRenaming={renamingNodeId === bookmark.id}
                    handleNavigateToFolder={handleNavigateToFolder}
                    setRenamingNode={setRenamingNode}
                />
            ))}
        </>
    );
};

export const TextHeader = styled.div`
    height: 40px;
    width: 100%;
    padding: 0px ${({ theme }) => theme.px.small};

    display: flex;
    align-items: center;
`;
