import { OpenFin } from '@openfin/core';

import { MenuItemType } from '@common/utils/context-menu';

import { t } from '@common/api/i18next';

import { getCurrentSync } from '@client-platform/api';
import { BookmarkItemContextMenuItemTemplate, BookmarkItemContextMenuOptionType } from '@client-platform/shapes';

import {
    ALL_BOOKMARKS_FOLDER_TITLE,
    BookmarkFolder,
    DOCK_FAVORITE_BOOKMARK_PREFIX,
    FAVORITES_FOLDER_TITLE,
    getBookmarkFoldersList,
    getBookmarkNodeTitle
} from './bookmarks';

export const getSortedBookmarkFoldersContextMenuOptions = (
    bookmarkFolders: BookmarkFolder[],
    checkedNodeId: string,
    separators?: {
        afterAllBookmarks?: boolean;
        afterFavorites?: boolean;
    }
) => {
    const menuTemplate = bookmarkFolders.reduce(
        (template: BookmarkItemContextMenuItemTemplate[], folder: BookmarkFolder) => {
            const isAllBookmarksFolder = folder.title === ALL_BOOKMARKS_FOLDER_TITLE;
            const isFavoritesFolder = folder.title === FAVORITES_FOLDER_TITLE;

            template.push({
                type: MenuItemType.Checkbox,
                label: getBookmarkNodeTitle(folder.title),
                checked: checkedNodeId === folder.id,
                data: {
                    type: BookmarkItemContextMenuOptionType.Move,
                    folderId: folder.id
                }
            });

            if (
                (separators?.afterAllBookmarks && isAllBookmarksFolder) ||
                (separators?.afterFavorites && isFavoritesFolder)
            ) {
                template.push({ type: 'separator' });
            }

            return template;
        },
        []
    );

    return menuTemplate;
};

const getBookmarkFoldersContextMenuOptions = async (
    callerIdentity: OpenFin.Identity,
    checkedNodeId: string
): Promise<BookmarkItemContextMenuItemTemplate[]> => {
    const browser = getCurrentSync().Browser.wrapSync(callerIdentity);

    // eslint-disable-next-line no-underscore-dangle
    const rootNode = await browser._bookmarks._getBookmarkNode();
    const bookmarkFolders = getBookmarkFoldersList(rootNode);

    return getSortedBookmarkFoldersContextMenuOptions(bookmarkFolders, checkedNodeId, {
        afterAllBookmarks: true,
        afterFavorites: true
    });
};

export const getBookmarkItemContextMenuTemplate = async (
    callerIdentity: OpenFin.Identity,
    bookmarkId: string
): Promise<BookmarkItemContextMenuItemTemplate[]> => {
    const browser = getCurrentSync().Browser.wrapSync(callerIdentity);
    // eslint-disable-next-line no-underscore-dangle
    const targetNode = await browser._bookmarks._getBookmarkNode(bookmarkId);

    // Dock favorites can't be moved out of favorites folder or renamed
    if (targetNode.url?.startsWith(DOCK_FAVORITE_BOOKMARK_PREFIX)) {
        return [
            {
                type: MenuItemType.Label,
                label: t('global.delete'),
                data: {
                    type: BookmarkItemContextMenuOptionType.Delete
                }
            }
        ];
    }

    return [
        {
            type: MenuItemType.Label,
            label: t('bookmarks.editName'),
            data: {
                type: BookmarkItemContextMenuOptionType.EditName
            }
        },
        ...(targetNode.url
            ? [
                  {
                      type: MenuItemType.Submenu,
                      label: t('bookmarks.move'),
                      submenu: await getBookmarkFoldersContextMenuOptions(callerIdentity, targetNode.parentId)
                  }
              ]
            : []),
        { type: MenuItemType.Separator, data: undefined },
        {
            type: MenuItemType.Label,
            label: t('global.delete'),
            data: {
                type: BookmarkItemContextMenuOptionType.Delete
            }
        }
    ];
};
