import { useCallback, useEffect, useState } from 'react';

import { FAVORITES_FOLDER_TITLE, sortBookmarks } from '@common/utils/bookmarks';
import { currentOFIdentity } from '@common/utils/window';

import { BookmarkNode } from '@common/api/pages/shapes';

import { getCurrentSync } from '@client-platform/api';

/**
 * Custom hook that retrieves the root node corresponding to All Bookmarks on initial render and
 * provides actions to update bookmark results.
 *
 * @returns {Object} An object containing the following:
 * - `bookmarksList` : `BookmarkNode[]` - A sorted list of the current folder's child bookmarks and folders.
 * - `currentFolder` : `BookmarkNode` - The currently active folder.
 * - `deleteBookmark` : A function to update currentFolder and bookmarksList given a specific folderId.
 */
export const useUpdateBookmarkResultsActions = () => {
    const [bookmarksList, setBookmarksList] = useState<BookmarkNode[]>();
    const [currentFolder, setCurrentFolder] = useState<BookmarkNode>(undefined);

    // Retrieves updated bookmark results. Should be called on writes, navigations, and search query resets.
    // If no folderId is provided, the current folder is refreshed. If no current folder is set, the root node is retrieved/refreshed.
    const updateBookmarksResults = useCallback(
        async (folderId?: string) => {
            const browser = getCurrentSync().Browser.wrapSync(currentOFIdentity);
            const searchFolderId = folderId ? folderId : currentFolder?.id;

            // eslint-disable-next-line no-underscore-dangle
            const folder = await browser._bookmarks._getBookmarkNode(searchFolderId);
            const isFavoritesFolder = folder.title === FAVORITES_FOLDER_TITLE;

            // Order of items in favorites folder is controlled by Dock favorites
            setBookmarksList(isFavoritesFolder ? folder.children : folder.children.sort(sortBookmarks));
            setCurrentFolder(folder);
        },
        [currentFolder?.id]
    );

    // Retrieve root node on render
    useEffect(() => {
        updateBookmarksResults();
    }, []);

    return {
        bookmarksList,
        currentFolder,
        updateBookmarksResults
    };
};
